import React, { Component } from 'react';
import firebase from '../Database';
import { Link } from 'react-router-dom';
import ReactQuill from 'react-quill';
//import 'react-quill/dist/quill.snow.css'

class Edit extends Component {

  constructor(props) {
    super(props);
    this.docRef = firebase.firestore().collection('categorias');
    this.unsubscribe= null;
    this.state = {
      key: '',
      title: '',
      description: '',
      category:'',
      categorias:[]
    };
  }

  componentDidMount() {
    const ref = firebase.firestore().collection('notes').doc(this.props.match.params.id);
    ref.get().then((doc) => {
      if (doc.exists) {
        const Post = doc.data();
        this.setState({
          key: doc.id,
          title: Post.title,
          description: Post.description,
          category: Post.category,
        });
      } else {
        console.log("error");
      }
    });
    this.unsubscribe=this.docRef.onSnapshot(this.getCategory);
  }



  onChange = (e) => {
    const state = this.state
    state[e.target.name] = e.target.value;
    this.setState({Post:state});
  }

  
  onHandleChange = (e) => {
    this.setState({description: e})
    console.log(this.state.description);
  }

  onSubmit = (e) => {
    e.preventDefault();

    const { title, description, category } = this.state;

    const updateRef = firebase.firestore().collection('notes').doc(this.state.key);
    updateRef.set({
      title,
      description,
      category
    }).then((docRef) => {
      this.setState({
        key: '',
        title: '',
        description: '',
        category:''
      });
      this.props.history.push("/show/"+this.props.match.params.id)
    })
    .catch((error) => {
      console.error("Error", error);
    });
  }


  getCategory = (querySnapshot) => {

    const categorias=[];

        querySnapshot.forEach((doc) => {
          const { title} = doc.data();
          categorias.push({
            key: doc.id,
            doc, // DocumentSnapshot
            title
          });
        });
        this.setState({
          categorias
        });
      }

  render() {
    return (
      <div>
      <header>
          <nav class="navbar navbar-default navbar-fixed-top">
            <div class="container">
              <div class="navbar-header navbar-brand">
              <Link to="/"><img alt="Inicio Multi Wiki" src={require('./LogoWordSmall.png')} height="45px"></img></Link>                          
              </div>              
            </div>
          </nav>                
        </header>
        <div class="container centerbody">
          <div class="panel panel-default">
            <div class="panel-heading">
                <br></br>
              <h3 class="panel-title">
                Editar Post
              </h3>
            </div>
            <div class="panel-body">
              
              <form onSubmit={this.onSubmit}>
                <div class="form-group">
                  <label for="title">Título</label>
                  <input type="text" class="form-control" name="title" value={this.state.title} onChange={this.onChange} placeholder="Agrega un título a tu post" />
                </div>
                <div class="form-group">
                <label for="title">Contenido</label>                  
                  <ReactQuill
                    modules={Edit.modules}
                    formats={Edit.formats}
                    value={this.state.description}
                    placeholder="¿Qué quieres compartir?"
                    onChange={this.onHandleChange}
                  />
                </div>
                <div class="form-group" onChange={category => this.onChange(category)}>
                    <select name="category">
                    <option value="" selected="selected"> {this.state.category} </option>
                    {this.state.categorias.map(Valor =>
                      <option  value={Valor.title}>{Valor.title}</option>)}
                  </select>
                  </div>
                <button type="submit" class="btn btn-primary">Publicar</button>
              </form>                            
            </div>
          </div>
        </div>
      </div>
    );
  }
}

Edit.modules={
  toolbar: [
    [{header: '1'},{header:'2'},{font:[]}],
    ['bold','italic','underline','strike','blockquote'],
    [{'list':'ordered'},{'list':'bullet'}],
    ['link','image','video'],
    ['clean'],
    ['code-block']
  ]
};

Edit.formats=[
  'header','font','size',
  'bold','italic','underline','strike','blockquote',
  'list','bullet',
  'link','image','video','code-block'
]

export default Edit;
