import React, { Component } from 'react';
import { Link, Router } from 'react-router-dom';
import './App.css';
import LogoutButton from './components/Logout';
import List from './components/Searchbar';
import { withFirebase } from './components/Firebase';
import AuthUserContext from './components/Session/context';

class App extends Component {
  constructor(props) {
    super(props);    
    this.ref = this.props.firebase.firestored();
    this.users = this.props.firebase.users();
    this.unsubscribe = null;
    this.state = {
      posts: [],
      authUser: null,
      email: ""
    };
  }

  onCollectionUpdate = (querySnapshot) => {
    const posts = [];
    querySnapshot.forEach((doc) => {
      const { title, description, author, category } = doc.data();
      posts.push({
        key: doc.id,
        doc, // DocumentSnapshot
        title,
        description,
        author,
        category,
      });
    });
    this.setState({
      posts
    });
  }

  componentDidMount() {
    this.unsubscribe = this.ref.onSnapshot(this.onCollectionUpdate);

    this.listener = this.props.firebase.auth.onAuthStateChanged(
      authUser => {
        authUser ? this.setState({ authUser}) : this.setState({ authUser: null }); 
      },
    );
  }

  componentWillUnmount() {
    this.listener();
  }

  render() {
    return (      
      <AuthUserContext.Provider value={this.state.authUser, this.users}>
        <div>
          <AuthUserContext.Consumer>
            {this.state.authUser, user =>{              
              if(this.state.authUser && (user.currentUser.email == "someone@gmail.com"))
              {
                return <this.NavigationAdmin/>
              }else if(this.state.authUser)
              {
                return <this.NavigationAuth/>
              }else
              {
                return <this.NavigationNonAuth/>
              };
            }
            }            
          </AuthUserContext.Consumer>          
        </div>
      </AuthUserContext.Provider>      
    );
  }

  NavigationAdmin= () => (    
    <div>
    <header>
      <nav class="navbar navbar-default navbar-fixed-top">
        <div class="container">
          <div class="navbar-header navbar-brand">
            <Link to="/"><img alt="Inicio Multi Wiki" src="LogoWordSmall.png" height="45px"></img></Link>                          
          </div>                        
          <div class="navbar-body">           
            <Link to="/showcategory" class="btn btn-primary">Categorías</Link>
            <Link to="/Createcategory" class="btn btn-primary">Nueva categoria</Link>
            <Link to="/create" class="btn btn-primary">Nuevo Post</Link>
            <Link to="/Passwordchange" class="btn btn-primary">Cambiar contraseña</Link>
            <LogoutButton/>                              
          </div>
        </div>
      </nav>                
    </header>
    <div class="container">
      <br></br><h5 class="panel-title" align="right">Hola Admin: {this.users.currentUser.email}</h5>
      <List items={this.state.posts} />
    </div>        
  </div>
);


  NavigationAuth = () => (      
      <div>
      <header>
        <nav class="navbar navbar-default navbar-fixed-top">
          <div class="container">
            <div class="navbar-header navbar-brand">
              <Link to="/"><img alt="Inicio Multi Wiki" src="LogoWordSmall.png" height="45px"></img></Link>                          
            </div>                        
            <div class="navbar-body">           
              <Link to="/showcategory" class="btn btn-primary">Categorías</Link>
              <Link to="/create" class="btn btn-primary">Nuevo Post</Link>
              <Link to="/Passwordchange" class="btn btn-primary">Cambiar contraseña</Link>
            <LogoutButton/>                              
            </div>
          </div>
        </nav>                
      </header>
      <div class="container">
        <br></br><h5 class="panel-title" align="right">Hola {this.users.currentUser.email}</h5>
        <List items={this.state.posts} />
      </div>        
    </div>   
  );
  
  NavigationNonAuth = () => (      
      <div>
        <header>
          <nav class="navbar navbar-default navbar-fixed-top">
            <div class="container">
              <div class="navbar-header navbar-brand">
                <Link to="/"><img alt="Inicio Multi Wiki" src="LogoWordSmall.png" height="45px"></img></Link>                          
              </div>                        
              <div class="navbar-body">           
                <Link to="/Login" class="btn btn-primary">Iniciar Sesión</Link>
                <Link to="/showcategory" class="btn btn-primary">Categorías</Link>                              
              </div>
            </div>
          </nav>                
        </header>
        <div class="container">
          <List items={this.state.posts} />
        </div>        
      </div>      
  );  
}

export default withFirebase(App);
