import React, { Component } from 'react';
import { withFirebase } from './Firebase';
import { Link } from 'react-router-dom';

class Showcategory extends Component {
  constructor(props) {
    super(props);    
    this.ref = this.props.firebase.firestorec();
    this.unsubscribe = null;
    this.state = {
      categorias: []
    };
  }

  onCollectionUpdate = (querySnapshot) => {
    const categorias = [];
    querySnapshot.forEach((doc) => {
      const { title} = doc.data();
      categorias.push({
        key: doc.id,
        doc, // DocumentSnapshot
        title
      });
    });
    this.setState({
      categorias
    });
  }

  componentDidMount() {
    this.unsubscribe = this.ref.onSnapshot(this.onCollectionUpdate);
  }

  render() {
    return (
      <div>
        <header>
          <nav class="navbar navbar-default navbar-fixed-top">
            <div class="container">
              <div class="navbar-header navbar-brand">
                <Link to="/"><img alt="Inicio Multi Wiki" src="LogoWordSmall.png" height="45px"></img></Link>                          
              </div>              
            </div>
          </nav>                
        </header>                      
      <div class="cantainer centerbody">
        <div class="containerCategoria">
          {this.state.categorias.map(Post =>
            <button class="btn-outline-secondary elementoCategoria">               
              <td><img alt="Categoria" src="Book.png" height="100px">
              </img><Link to={`/showpostcat/${Post.key}`}>{Post.title}</Link></td>                  
            </button>
          )}
        </div>
      </div>
    </div>
    );
  }
}

export default withFirebase(Showcategory);
